import { Notification } from 'element-ui';

/**
 * @description: 
 * @param {string} title 
 * @param {string} msg 
 * @return: void
 */
export function showSuccessNotify(title, msg) {
  Notification.success({
    title: title,
    message: msg,
    duration: 2000,
    position: 'bottom-right',
    showClose: true
  });
}

